<script>
import { Button } from "iview";
import { CountUp } from "countup.js";
import { imgUrl } from "../../utils";
import {
  getProjectNewest,
  getProjectSuperior,
  getProjectWeek,
  getProjectCase,
  getProjectSummary
} from "../../api/public";
export default {
  components: {
    [Button.name]: Button
  },
  data() {
    return {
      imgUrl: imgUrl,
      projectCase: [], //	成功案列
      projectWeek: [], //每周推荐项目
      projectSuperior: [], //获取优质项目
      projectNewest: [], //获取最新项目
      projectSummary: [] //获取最新项目
    };
  },
  methods: {
    //	成功案列
    getProjectCase() {
      getProjectCase({ size: 4 }).then(res => {
        this.projectCase = res.data.data.rows;
      });
    },
    //每周推荐项目
    getProjectWeek() {
      getProjectWeek({ size: 8 }).then(res => {
        this.projectWeek = res.data.data;
      });
    },
    //获取优质项目
    getProjectSuperior() {
      getProjectSuperior({ size: 8 }).then(res => {
        this.projectSuperior = res.data.data;
      });
    },
    //获取最新项目
    getProjectNewest() {
      getProjectNewest({ size: 8 }).then(res => {
        this.projectNewest = res.data.data.rows;
      });
    },
    //获取项目主页信息
    getProjectSummary() {
      getProjectSummary().then(res => {
        this.projectSummary = res.data.data;
        let numberUp1 = new CountUp(
          "numberUp1",
          this.projectSummary.projectUser
        );
        let numberUp2 = new CountUp(
          "numberUp2",
          this.projectSummary.projectMoneySum
        );
        let numberUp3 = new CountUp(
          "numberUp3",
          this.projectSummary.projectTrading
        );
        numberUp1.start() || numberUp2.start() || numberUp3.start();
      });
    }
  },
  created() {
    this.getProjectCase();
    this.getProjectWeek();
    this.getProjectSuperior();
    this.getProjectNewest();
    this.getProjectSummary();
  }
};
</script>
